import React from "react";

const FormatsDesktopLazy = React.lazy(() => import("./formatsDesktop"));
const FormatsMobileLazy = React.lazy(() => import("./formatsMobile"));

const Formats = ({ language, isMobile }) => {
  return (
    <>
      {isMobile ? (
        <FormatsMobileLazy language={language} />
      ) : (
        <FormatsDesktopLazy language={language} />
      )}
    </>
  );
};

export default Formats;
