import React from "react";

const EcommerceDesktopLazy = React.lazy(() => import("./ecommerceDesktop"));
const EcommerceMobileLazy = React.lazy(() => import("./ecommerceMobile"));

const Ecommerce = ({ language, isMobile }) => {
  return (
    <>
      {isMobile ? (
        <EcommerceMobileLazy language={language} />
      ) : (
        <EcommerceDesktopLazy language={language} />
      )}
    </>
  );
};

export default Ecommerce;
