import React, { useEffect, useState } from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";

// styles
import GlobalStyles from "./assets/styles/global";
import "./App.css";


// components
import Header from "./Components/Header/header";
import Intro from "./Components/Intro/intro";
import Technology from "./Components/Technology/technology";
import Formats from "./Components/Formats/formats";
import Compare from "./Components/Compare/compare";
import WhyTry from "./Components/WhyTry/whytry";
import ECommerce from "./Components/ECommerce/ecommerce";
import Future from "./Components/Future/future";
import Footer from "./Components/Footer/footer";

// const Privacy = React.lazy(() => import("./Components/Privacy"));
// const Terms = React.lazy(() => import("./Components/Terms"));
// const UserAgreement = React.lazy(() => import("./Components/UserAgreement"));
// const AboutUs = React.lazy(() => import("./Components/AboutUs/AboutUs"));

const App = () => {
  // Scroll Listener
  const [scrollY, setScrollY] = useState(0);

  useEffect(() => {
    const handleScroll = (event) => {
      setScrollY(window.scrollY);
    };

    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  const [isMobile, setIsMobile] = useState(false);
  const [isTablet, setIsTablet] = useState(false);

  useEffect(() => {
    onSubscribe();

    return () => offSubscribe();
  }, []);

  // Resize Listener
  const breakpoint = 650;
  const breakpoint_tablet = 1100;

  const [widthWindow, setWidthWindow] = useState(window.screen.width);

  const onSubscribe = () => window.addEventListener("resize", windowResize);
  const offSubscribe = () => window.removeEventListener("resize", windowResize);

  const windowResize = () => {
    setWidthWindow(window.innerWidth);
  };

  useEffect(() => {
    if (widthWindow <= breakpoint) {
      setIsMobile(true);
    } else {
      setIsMobile(false);
    }

    if (widthWindow <= breakpoint_tablet && widthWindow > breakpoint) {
      setIsTablet(true);
    } else {
      setIsTablet(false);
    }

    console.log(widthWindow, widthWindow <= breakpoint, widthWindow <= breakpoint_tablet && widthWindow > breakpoint);
  }, [widthWindow]);

  // Localization change

  const [language, setLanguage] = useState(
    localStorage.getItem("Language") ? localStorage.getItem("Language") : "Ru"
  );

  return (
    <Router basename="/">
      <React.Suspense>
        <Routes>
          <Route
            path="/"
            element={
              <>
                <Header
                  changeLng={setLanguage}
                  language={language}
                  isMobile={isMobile}
                  isTablet={isTablet}
                  scrollY={scrollY}
                />
                <Intro language={language} isMobile={isMobile} />
                <Technology language={language} isMobile={isMobile} />
                <Formats language={language} isMobile={isMobile} />
                <Compare language={language} isMobile={isMobile} />
                <WhyTry language={language} isMobile={isMobile} />
                <ECommerce language={language} isMobile={isMobile} />
                <Future language={language} isMobile={isMobile} />
                <Footer language={language} isMobile={isMobile} />
              </>
            }
          />

          {/* <Route
            path="/about"
            element={
              <>
                <Header
                  language={language}
                  isMobile={isMobile}
                  scrollY={scrollY}
                />
                <AboutUs language={language} isMobile={isMobile} exact />
              </>
            }
          />
          <Route path="/privacy" element={<Privacy />} exact />
          <Route path="/terms" element={<Terms />} exact />
          <Route path="/u_agreement" element={<UserAgreement />} exact /> */}
        </Routes>

        <GlobalStyles />
      </React.Suspense>
    </Router>
  );
};

export default App;
